<template>
<div>

  <el-breadcrumb separator=">" class="jm-margin-bottom20">
    <span class="el-breadcrumb__item"><span></span></span>
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <el-row :gutter="20">
    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-1 ">

          <p><span class="number">{{summaryData.amount}}</span><span class="unit"></span></p>
          <p class="title"></p>

        </div>
      </div>
    </el-col>

    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-2 ">

          <p><span class="number">{{summaryData.count}}</span><span class="unit"></span></p>
          <p class="title"></p>
        </div>
      </div>
    </el-col>

    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-3 ">

          <p><span class="number">{{summaryData.customerCount}}</span><span class="unit"></span></p>
          <p class="title"></p>
        </div>
      </div>
    </el-col>

    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-4 ">

          <p><span class="number">{{summaryData.partnerCount}}</span><span class="unit"></span></p>
          <p class="title"></p>
        </div>
      </div>
    </el-col>

    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-5 ">

          <p><span class="number">{{summaryData.partnerIncome}}</span><span class="unit"></span></p>
          <p class="title"></p>
        </div>
      </div>
    </el-col>

    <el-col :xs="8" :sm="8" :md="8" :lg="4">
      <div class="grid-content">
        <div class="box-summary summary-bg-6 ">

          <p><span class="number">{{summaryData.customerPay}}</span><span class="unit"></span></p>
          <p class="title"></p>
        </div>
      </div>
    </el-col>

  </el-row>
  <!-- end of summary -->

  <el-row class="jm-grid-border-1px">
    <header class="panel-header overview-panel-header jm-grid-box-bg-white jm-grid-border-bottom-1px">
      <span class="title"></span>


      <div class="overview-filter">
<el-select v-model="selectedArea" change="areaChanged" placeholder="">
    <el-option
      v-for="item in areaOptions"
      :label="item.label"
      :value="item.value" :key="item">
    </el-option>
  </el-select>
        </div>


    </header>
    <el-col :xs="24" :sm="24" :md="24" :lg="8">
      <div class="grid-content jm-grid-box-bg-white">

        <div class="box chart">
          <div class="content" ref='overviewLevelPieChart'>

          </div>

        </div>
      </div>
    </el-col>
    <el-col :xs="24" :sm="24" :md="24" :lg="8">
      <div class="grid-content jm-grid-box-bg-white">

        <div class="box  chart ">
          <div class="content" ref='overviewAreaPieChart'>

          </div>
        </div>

      </div>
    </el-col>

    <el-col :xs="24" :sm="24" :md="24" :lg="8">
      <div class="grid-content jm-grid-box-bg-white">

        <div class="box  chart ">
          <div class="content" ref='overviewCustomerPieChart'>

          </div>
        </div>

      </div>
    </el-col>
  </el-row>

</div>

</template>

<script>
import {
  getOverview,
  starLevel,
  partner,
  customer
} from '@/service/api'

import echarts from 'echarts';

export default {
  name: 'overview',
  components: {
  },
  watch: {
    //  selectedArea 
    selectedArea: function (newAreaCode) {
      this.selectedAreaCode = newAreaCode
      this.areaChanged()
    }
  },
  methods: {
    areaChanged() {
      this.loadStarLevel();
      this.loadPartner();
      this.loadCustomer();
    },
    loadStarLevel() {
      var vm = this;
      var area = vm.selectedAreaCode;
      starLevel(area).then(function (response) {
          var chartData = response.data;
          var chartId = 'overviewLevelPieChart'
          vm.loadChartOption(chartData, chartId)
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    loadPartner() {
      var vm = this;
      var area = vm.selectedAreaCode;
      partner(area).then(function (response) {
          var chartData = response.data;
          var chartId = 'overviewAreaPieChart'
          vm.loadChartOption(chartData, chartId)
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    loadCustomer() {
      var vm = this;
      var area = vm.selectedAreaCode;
      customer(area).then(function (response) {
          var chartData = response.data;
          var chartId = 'overviewCustomerPieChart'
          vm.loadChartOption(chartData, chartId)
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    loadChartOption(chartData, chartId) {
      var vm = this;
      vm[chartId] = echarts.init(vm.$refs[chartId]);
      // 
      var option = {
        color:['#1c7099','#1790cf', '#1bb2d8', '#99d2dd', '#88b0bb','#22b1e6','#4598ff'],
        title: {
          text: chartData.title,
          left: 'center',
          bottom: '5%',
          textStyle: {
            color: '#666',
            fontWeight: 'normal',
            fontSize: 14
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        series: [{
          name: chartData.seriesName,
          type: 'pie',
          radius: '55%',
          center: ['50%', '50%'],
          data: chartData.seriesData,
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      };
      vm[chartId].setOption(option)
    }
  },
  data() {
    return {
      overviewLevelPieChart: null,
      overviewAreaPieChart: null,
      overviewCustomerPieChart: null,
      summaryData: {},
      areaOptions: [{ value: '86', label: ''},
      { value: '86011', label: '' },
      { value: '86012', label: '' },
      { value: '86013', label: '' },
      { value: '86014', label: '' },
      { value: '86015', label: '' },
      { value: '86021', label: '' },
      { value: '86022', label: '' },
      { value: '86023', label: '' },
      { value: '86031', label: '' },
      { value: '86032', label: '' },
      { value: '86033', label: '' },
      { value: '86034', label: '' },
      { value: '86035', label: '' },
      { value: '86036', label: '' },
      { value: '86037', label: '' },
      { value: '86041', label: '' },
      { value: '86042', label: '' },
      { value: '86043', label: '' },
      { value: '86044', label: '' },
      { value: '86045', label: '' },
      { value: '86046', label: '' },
      { value: '86050', label: '' },
      { value: '86051', label: '' },
      { value: '86052', label: '' },
      { value: '86053', label: '' },
      { value: '86054', label: '' },
      { value: '86061', label: '' },
      { value: '86062', label: '' },
      { value: '86063', label: '' },
      { value: '86064', label: '' },
      { value: '86065', label: '' },
      { value: '86071', label: '' },
      { value: '86081', label: '' },
      { value: '86082', label: '' }],
      selectedArea: '',
      selectedAreaCode: '86'
    }
  },
  mounted() {
    var vm = this;

    getOverview().then(function (response) {
        vm.summaryData = response.data;
      })
      .catch(function (error) {
        console.log(error);
      });

    this.loadStarLevel();
    this.loadPartner();
    this.loadCustomer();

    window.addEventListener('resize', chartsResize, false);

    function chartsResize() {
      if (vm.overviewLevelPieChart) {
        vm.overviewLevelPieChart.resize()
      }

      if (vm.overviewAreaPieChart) {
        vm.overviewAreaPieChart.resize()
      }

      if (vm.overviewCustomerPieChart) {
        vm.overviewCustomerPieChart.resize()
      }

    }
  }
}

</script>

<style lang="scss">
  .box-summary {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    color: #fff;
    width: 100%;
    height: 135px;
    border-radius: 5px;
    p {
      margin-top: 1em;
      margin-bottom: 1em;
    }
    .number {
      font-size: 28px;
    }
    .unit {
      font-size: 14px;
      margin-left: 10px;
      margin-right: 30px;
    }
    .title {
      font-size: 18px;
      margin-right: 30px;
    }
  }

  @for $i from 1 through 6 {
    .summary-bg-#{$i} {
      background: url(../assets/images/overview/summary-#{$i}-bg.png) no-repeat;
      background-size: 100% 100%;
    }
  }

  @media (max-width: 1199px) {
    .box-summary {
      margin-bottom: 20px;
    }
  }

  .overview-panel-header {
    display: flex;
    justify-content: flex-end;
    .title {
      align-self: flex-start;
      margin-right: auto;
    }
  }

  .overview-filter {
    font-size: 14px;
    .el-input__inner {
      height: 32px;
    }
  }


</style>



